import React, { useState, useEffect } from 'react';
import './App.css';
import { HeatmapSeries, XYPlot } from 'react-vis';

function App() {
  const [networkArchitecture, setNetworkArchitecture] = useState(null);
  const [weights, setWeights] = useState(null);
  const [userInput, setUserInput] = useState('');
  const [aiResponse, setAiResponse] = useState('');

  useEffect(() => {
    const fetchNetworkArchitecture = async () => {
      try {
        const response = await fetch('http://localhost:8000/network_architecture');
        const data = await response.json();
        setNetworkArchitecture(data);
      } catch (error) {
        console.error("Error fetching network architecture:", error);
      }
    };

    const fetchWeights = async () => {
      try {
        const response = await fetch('http://localhost:8000/weights');
        const data = await response.json();
        setWeights(data);
      } catch (error) {
        console.error("Error fetching weights:", error);
      }
    };

    fetchNetworkArchitecture();
    fetchWeights();
  }, []);

  // Prepare heatmap data (example: first layer weights)
  const heatmapData = weights && weights.length > 0 ? weights[0].data.map((value, index) => ({
    x: index,
    y: 0,
    color: value
  })) : [];

  const handleInputChange = (event) => {
    setUserInput(event.target.value);
  };

  const handleInteract = async () => {
    try {
      const response = await fetch('http://localhost:8000/interact', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: `user_input=${userInput}`,
      });
      const data = await response.text();
      setAiResponse(data);
    } catch (error) {
      console.error("Error interacting with AI:", error);
      setAiResponse("Error: Could not get response from AI.");
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>Self-Evolving AI</h1>
      </header>

      <section>
        <h2>Network Architecture</h2>
        {networkArchitecture && (
          <div>
            <p>Number of Layers: {networkArchitecture.num_layers}</p>
            <p>Neurons per Layer: {networkArchitecture.num_neurons.join(', ')}</p>
          </div>
        )}
      </section>

      <section>
        <h2>Weights (First Layer)</h2>
        {weights && (
          <XYPlot width={300} height={100}>
            <HeatmapSeries
              data={heatmapData}
              colorRange={["white", "blue"]}
            />
          </XYPlot>
        )}
      </section>

      <section>
        <h2>Code Modifications</h2>
        <p>Code modification logging not implemented yet.</p>
      </section>

      <section>
        <h2>Training Data</h2>
        <p>Training data display not implemented yet.</p>
      </section>

      <section>
        <h2>Interact with AI</h2>
        <input
          type="text"
          value={userInput}
          onChange={handleInputChange}
          placeholder="Enter your input"
        />
        <button onClick={handleInteract}>Interact</button>
        {aiResponse && (
          <div>
            <h3>AI Response:</h3>
            <p>{aiResponse}</p>
          </div>
        )}
      </section>
    </div>
  );
}

export default App;
